"use client";

import React, { useState, useEffect, useRef } from "react";
import { Brain, Star, Trophy, Clock, Settings, LogOut, User, ChevronDown, Volume2, Play, Pause } from "lucide-react";

interface Module {
  id: string;
  name: string;
  icon: string;
  description: string;
  color: string;
  bgColor: string;
  progress: number;
  stars: number;
  isUnlocked: boolean;
  audioUrl?: string;
  neuroTip?: string;
}

interface UserStats {
  name: string;
  totalStars: number;
  streak: number;
  timeSpent: number;
  level: number;
}

interface AudioState {
  isPlaying: boolean;
  currentModule: string | null;
}

export default function DashboardPage() {
  // Simulación de Clerk hooks - reemplaza con los reales
  const user = {
    firstName: "María",
    lastName: "García",
    username: "maria_g",
    imageUrl: null,
    emailAddresses: [{ emailAddress: "maria@example.com" }]
  };

  const signOut = async () => {
    // Simular cierre de sesión
    console.log("Cerrando sesión...");
    return Promise.resolve();
  };

  const router = {
    push: (path: string) => {
      console.log(`Navegando a: ${path}`);
    }
  };

  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isSigningOut, setIsSigningOut] = useState(false);
  const [audioState, setAudioState] = useState<AudioState>({ isPlaying: false, currentModule: null });
  const menuRef = useRef<HTMLDivElement>(null);
  const audioRef = useRef<HTMLAudioElement>(null);
  
  const [userStats, setUserStats] = useState<UserStats>({
    name: "Nombre/Apodo",
    totalStars: 47,
    streak: 5,
    timeSpent: 125,
    level: 3,
  });

  const [modules, setModules] = useState<Module[]>([
    {
      id: "conjuntos",
      name: "CONJUNTOS",
      icon: "./images/icons/conjuntos.png",
      description: "Agrupa elementos",
      color: "#FF69B4",
      bgColor: "from-pink-200 to-purple-200",
      progress: 85,
      stars: 3,
      isUnlocked: true,
      audioUrl: "/audio/conjuntos-motivacion.mp3",
      neuroTip: "Agrupar objetos fortalece las conexiones neuronales del lóbulo parietal, mejorando la organización mental."
    },
    {
      id: "numeracion",
      name: "NUMERACIÓN",
      icon: "./images/icons/numeracion.png",
      description: "Aprende números",
      color: "#4169E1",
      bgColor: "from-blue-200 to-indigo-200",
      progress: 60,
      stars: 2,
      isUnlocked: true,
      audioUrl: "/audio/numeracion-motivacion.mp3",
      neuroTip: "Los números activan el área cerebral responsable del procesamiento matemático, fortaleciendo la memoria de trabajo."
    },
    {
      id: "suma",
      name: "SUMA",
      icon: "./images/icons/suma.png",
      description: "Suma fácil",
      color: "#32CD32",
      bgColor: "from-green-200 to-emerald-200",
      progress: 40,
      stars: 1,
      isUnlocked: true,
      audioUrl: "/audio/suma-motivacion.mp3",
      neuroTip: "Sumar desarrolla la flexibilidad cognitiva y mejora las conexiones entre ambos hemisferios cerebrales."
    },
    {
      id: "resta",
      name: "RESTA",
      icon: "./images/icons/resta.png",
      description: "Resta simple",
      color: "#FF4500",
      bgColor: "from-orange-200 to-red-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
      audioUrl: "/audio/resta-motivacion.mp3",
      neuroTip: "La resta estimula el pensamiento analítico y fortalece la corteza prefrontal."
    },
    {
      id: "multiplicacion",
      name: "MULTIPLICACIÓN",
      icon: "./images/icons/multiplicacion.png",
      description: "Multiplica",
      color: "#8B4513",
      bgColor: "from-yellow-200 to-orange-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
      audioUrl: "/audio/multiplicacion-motivacion.mp3",
      neuroTip: "Las tablas de multiplicar crean patrones neuronales que mejoran la velocidad de procesamiento mental."
    },
    {
      id: "division",
      name: "DIVISIÓN",
      icon: "./images/icons/division.png",
      description: "Divide y reparte",
      color: "#20B2AA",
      bgColor: "from-cyan-200 to-teal-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
      audioUrl: "/audio/division-motivacion.mp3",
      neuroTip: "Dividir desarrolla el razonamiento lógico y la capacidad de resolución de problemas complejos."
    },
    {
      id: "geometria",
      name: "GEOMETRÍA",
      icon: "./images/icons/geometria.png",
      description: "Formas divertidas",
      color: "#9370DB",
      bgColor: "from-purple-200 to-pink-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
      audioUrl: "/audio/geometria-motivacion.mp3",
      neuroTip: "Las formas geométricas activan la percepción espacial y mejoran la coordinación visomotora."
    },
  ]);

  const [animatingModule, setAnimatingModule] = useState<string | null>(null);
  const [showNeuroTip, setShowNeuroTip] = useState<string | null>(null);

  // Audio motivacional general
  const motivationalAudios = [
    "/audio/general/eres-increible.mp3",
    "/audio/general/sigue-asi.mp3", 
    "/audio/general/lo-estas-haciendo-genial.mp3",
    "/audio/general/tu-cerebro-esta-creciendo.mp3"
  ];

  // Cerrar menú al hacer clic fuera
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsMenuOpen(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  // Actualizar nombre del usuario cuando esté disponible
  useEffect(() => {
    if (user) {
      setUserStats(prev => ({
        ...prev,
        name: user.firstName || user.username || "Usuario"
      }));
    }
  }, [user]);

  // Reproducir audio motivacional aleatorio al cargar
  useEffect(() => {
    const timer = setTimeout(() => {
      playMotivationalAudio();
    }, 2000);

    return () => clearTimeout(timer);
  }, []);

  const playMotivationalAudio = () => {
    const randomAudio = motivationalAudios[Math.floor(Math.random() * motivationalAudios.length)];
    // Aquí se reproduciría el audio real
    console.log(`Reproduciendo audio motivacional: ${randomAudio}`);
  };

  const handleModuleClick = (moduleId: string) => {
    const module = modules.find((m) => m.id === moduleId);
    if (!module?.isUnlocked) {
      setAnimatingModule(moduleId);
      setTimeout(() => setAnimatingModule(null), 600);
      return;
    }

    // Mostrar tip neurocientífico
    setShowNeuroTip(moduleId);
    setTimeout(() => setShowNeuroTip(null), 4000);

    // Reproducir audio específico del módulo
    if (module.audioUrl) {
      playModuleAudio(moduleId, module.audioUrl);
    }

    // Navegar después de un breve delay para mostrar el tip
    setTimeout(() => {
      router.push(`/modules/${moduleId}`);
    }, 1500);
  };

  const playModuleAudio = (moduleId: string, audioUrl: string) => {
    setAudioState({ isPlaying: true, currentModule: moduleId });
    
    // Aquí se reproduciría el audio real
    console.log(`Reproduciendo audio del módulo ${moduleId}: ${audioUrl}`);
    
    // Simular fin de reproducción
    setTimeout(() => {
      setAudioState({ isPlaying: false, currentModule: null });
    }, 3000);
  };

  const handleConfigClick = () => {
    setIsMenuOpen(false);
    router.push('/settings');
  };

  const handleSignOut = async () => {
    try {
      setIsSigningOut(true);
      setIsMenuOpen(false);
      
      await signOut();
      router.push('/');
    } catch (error) {
      console.error('Error al cerrar sesión:', error);
      setIsSigningOut(false);
    }
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  // Obtener las iniciales del usuario
  const getUserInitials = () => {
    if (user?.firstName && user?.lastName) {
      return `${user.firstName[0]}${user.lastName[0]}`.toUpperCase();
    } else if (user?.firstName) {
      return user.firstName[0].toUpperCase();
    } else if (user?.username) {
      return user.username[0].toUpperCase();
    }
    return "U";
  };

  const FloatingParticle = ({ delay }: { delay: number }) => (
    <div
      className="absolute w-2 h-2 bg-yellow-300 rounded-full opacity-60"
      style={{
        left: `${Math.random() * 100}%`,
        top: `${Math.random() * 100}%`,
        animation: `float ${2 + Math.random() * 2}s ease-in-out infinite`,
        animationDelay: `${delay}s`,
      }}
    />
  );

  const BrainWave = () => (
    <div className="fixed top-4 right-4 z-20">
      <div className="relative">
        <div className="w-16 h-16 bg-gradient-to-br from-purple-400 to-pink-500 rounded-full flex items-center justify-center animate-pulse-slow">
          <Brain className="w-8 h-8 text-white" />
        </div>
        <div className="absolute inset-0 bg-purple-400 rounded-full animate-ping opacity-30"></div>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-yellow-50 relative overflow-hidden">
      {/* Ondas cerebrales - indicador de actividad neuronal */}
      <BrainWave />

      {/* Tip neurocientífico flotante */}
      {showNeuroTip && (
        <div className="fixed top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-50 animate-fade-in-scale">
          <div className="bg-gradient-to-br from-purple-500 to-pink-600 text-white p-6 rounded-3xl shadow-2xl max-w-md mx-4">
            <div className="flex items-center gap-3 mb-3">
              <Brain className="w-8 h-8 animate-pulse" />
              <h3 className="font-bold text-lg">¡Dato Curioso!</h3>
            </div>
            <p className="text-sm leading-relaxed">
              {modules.find(m => m.id === showNeuroTip)?.neuroTip}
            </p>
          </div>
        </div>
      )}

      {/* Overlay de carga para cerrar sesión */}
      {isSigningOut && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-2xl p-8 shadow-2xl animate-bounce-in">
            <div className="flex items-center gap-4">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
              <p className="text-lg font-semibold text-gray-800">Cerrando sesión...</p>
            </div>
          </div>
        </div>
      )}

      {/* Partículas flotantes de fondo */}
      <div className="fixed inset-0 pointer-events-none">
        {[...Array(15)].map((_, i) => (
          <FloatingParticle key={i} delay={i * 0.3} />
        ))}
      </div>

      {/* Elementos decorativos de fondo mejorados */}
      <div className="fixed top-0 left-0 w-full h-full pointer-events-none z-0">
        <div className="absolute top-20 left-20 w-32 h-32 bg-yellow-200 rounded-full opacity-20 blur-2xl animate-pulse-slow"></div>
        <div className="absolute bottom-20 right-20 w-40 h-40 bg-blue-200 rounded-full opacity-20 blur-2xl animate-pulse-slow" style={{ animationDelay: "1s" }}></div>
        <div className="absolute top-1/2 left-1/4 w-24 h-24 bg-pink-200 rounded-full opacity-15 blur-xl animate-pulse-slow" style={{ animationDelay: "2s" }}></div>
        <div className="absolute bottom-1/3 left-3/4 w-28 h-28 bg-green-200 rounded-full opacity-15 blur-xl animate-pulse-slow" style={{ animationDelay: "0.5s" }}></div>
      </div>

      {/* Header mejorado */}
      <header className="relative z-10 flex justify-between items-center p-6 bg-white/80 backdrop-blur-sm shadow-lg">
        <div className="flex items-center gap-4">
          <div className="relative">
            <img src="/images/logo.png" alt="Logo MentaMática" className="w-12 h-12 animate-bounce-gentle" />
            <div className="absolute -top-1 -right-1 w-4 h-4 bg-yellow-400 rounded-full animate-ping"></div>
          </div>
          <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent animate-gradient">
            MentaMática
          </h1>
        </div>

        <div className="flex items-center gap-4">
          {/* Indicador de audio */}
          {audioState.isPlaying && (
            <div className="flex items-center gap-2 bg-green-100 px-3 py-2 rounded-full animate-pulse">
              <Volume2 className="w-4 h-4 text-green-600" />
              <span className="text-sm text-green-700 font-medium">Reproduciendo...</span>
            </div>
          )}

          <div className="flex items-center gap-2 bg-yellow-100 px-4 py-2 rounded-full shadow-md">
            <Star className="w-5 h-5 text-yellow-500 animate-spin-slow" />
            <span className="font-bold text-yellow-700">{userStats.totalStars}</span>
          </div>
          
          {/* Botón de Usuario con mejor manejo */}
          <div className="relative" ref={menuRef}>
            <button
              onClick={toggleMenu}
              disabled={isSigningOut}
              className="flex items-center gap-2 bg-gradient-to-br from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white px-4 py-2 rounded-full shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 focus:outline-none focus:ring-2 focus:ring-blue-400 focus:ring-opacity-50 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <div className="w-8 h-8 bg-white/20 rounded-full flex items-center justify-center text-sm font-bold">
                {user?.imageUrl ? (
                  <img 
                    src={user.imageUrl} 
                    alt="Avatar" 
                    className="w-full h-full rounded-full object-cover"
                  />
                ) : (
                  <span>{getUserInitials()}</span>
                )}
              </div>
              
              <span className="hidden sm:block font-medium text-sm">
                {user?.firstName || user?.username || "Usuario"}
              </span>
              
              <ChevronDown 
                className={`w-4 h-4 transition-transform duration-200 ${isMenuOpen ? 'rotate-180' : ''}`} 
              />
            </button>

            {/* Menú Desplegable mejorado */}
            {isMenuOpen && (
              <div className="absolute right-0 mt-2 w-56 bg-white/95 backdrop-blur-sm rounded-2xl shadow-2xl border border-gray-200/50 py-2 z-50 animate-slide-down">
                <div className="px-4 py-3 border-b border-gray-200/50">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white font-bold">
                      {user?.imageUrl ? (
                        <img 
                          src={user.imageUrl} 
                          alt="Avatar" 
                          className="w-full h-full rounded-full object-cover"
                        />
                      ) : (
                        <span>{getUserInitials()}</span>
                      )}
                    </div>
                    <div>
                      <p className="font-semibold text-gray-800">
                        {user?.firstName || user?.username || "Usuario"}
                      </p>
                      <p className="text-sm text-gray-600">
                        {user?.emailAddresses?.[0]?.emailAddress || ""}
                      </p>
                    </div>
                  </div>
                </div>

                <div className="py-1">
                  <button
                    onClick={handleConfigClick}
                    className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-100/80 transition-colors duration-200 group"
                  >
                    <div className="w-8 h-8 bg-gradient-to-br from-gray-400 to-gray-600 rounded-full flex items-center justify-center group-hover:from-gray-500 group-hover:to-gray-700 transition-all duration-200">
                      <Settings className="w-4 h-4 text-white" />
                    </div>
                    <span className="font-medium">Configuración</span>
                  </button>

                  <button
                    onClick={handleSignOut}
                    disabled={isSigningOut}
                    className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-red-50 hover:text-red-700 transition-colors duration-200 group disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <div className="w-8 h-8 bg-gradient-to-br from-red-400 to-red-600 rounded-full flex items-center justify-center group-hover:from-red-500 group-hover:to-red-700 transition-all duration-200">
                      {isSigningOut ? (
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                      ) : (
                        <LogOut className="w-4 h-4 text-white" />
                      )}
                    </div>
                    <span className="font-medium">
                      {isSigningOut ? "Cerrando..." : "Cerrar Sesión"}
                    </span>
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="relative z-10 container mx-auto px-6 py-8">
        {/* Welcome Section con neurociencia */}
        <div className="text-center mb-12">
          <h2 className="text-4xl font-bold mb-4 text-gray-800 animate-fade-in">
            ¡Hola {userStats.name}! 🧠✨
          </h2>
          <p className="text-xl text-gray-600 animate-fade-in-delay">
            Tu cerebro está listo para nuevas aventuras matemáticas
          </p>
          <div className="mt-4 bg-gradient-to-r from-purple-100 to-pink-100 p-4 rounded-2xl max-w-md mx-auto">
            <p className="text-sm text-purple-800 font-medium">
              💡 Cada ejercicio que haces crea nuevas conexiones neuronales
            </p>
          </div>
        </div>

        {/* Stats Bar mejorado */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-12">
          <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-4 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-gradient-to-br from-yellow-400 to-orange-400 rounded-full flex items-center justify-center">
                <Star className="w-6 h-6 text-white" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Estrellas</p>
                <p className="text-2xl font-bold text-gray-800">{userStats.totalStars}</p>
              </div>
            </div>
          </div>

          <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-4 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up" style={{ animationDelay: "0.1s" }}>
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-gradient-to-br from-green-400 to-emerald-400 rounded-full flex items-center justify-center">
                <Trophy className="w-6 h-6 text-white" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Racha</p>
                <p className="text-2xl font-bold text-gray-800">{userStats.streak} días</p>
              </div>
            </div>
          </div>

          <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-4 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up" style={{ animationDelay: "0.2s" }}>
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-gradient-to-br from-blue-400 to-indigo-400 rounded-full flex items-center justify-center">
                <Clock className="w-6 h-6 text-white" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Tiempo</p>
                <p className="text-2xl font-bold text-gray-800">{userStats.timeSpent}min</p>
              </div>
            </div>
          </div>

          <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-4 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up" style={{ animationDelay: "0.3s" }}>
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-gradient-to-br from-purple-400 to-pink-400 rounded-full flex items-center justify-center">
                <Brain className="w-6 h-6 text-white" />
              </div>
              <div>
                <p className="text-sm text-gray-600">Nivel</p>
                <p className="text-2xl font-bold text-gray-800">{userStats.level}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Modules Grid mejorado */}
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 max-w-6xl mx-auto">
          {modules.map((module, index) => (
            <div
              key={module.id}
              onClick={() => handleModuleClick(module.id)}
              className={`
                relative group cursor-pointer transform transition-all duration-500 hover:scale-105
                ${animatingModule === module.id ? "animate-shake" : ""}
                ${!module.isUnlocked ? "opacity-60" : ""}
                animate-slide-up
              `}
              style={{ animationDelay: `${index * 0.1}s` }}
            >
              <div className={`
                bg-gradient-to-br ${module.bgColor} rounded-3xl p-6 shadow-xl 
                hover:shadow-2xl transition-all duration-300 relative overflow-hidden
                ${module.isUnlocked ? "hover:-translate-y-2" : ""}
                min-h-[200px] flex flex-col items-center justify-center
              `}>
                {/* Efecto de brillo mejorado */}
                <div className="absolute inset-0 bg-gradient-to-r from-white/0 via-white/30 to-white/0 transform -skew-x-12 -translate-x-full group-hover:translate-x-full transition-transform duration-1000"></div>

                {/* Audio indicator */}
                {audioState.currentModule === module.id && (
                  <div className="absolute top-2 right-2 animate-pulse">
                    <Volume2 className="w-5 h-5 text-green-600" />
                  </div>
                )}

                {/* Lock overlay para módulos bloqueados */}
                {!module.isUnlocked && (
                  <div className="absolute inset-0 bg-black/20 rounded-3xl flex items-center justify-center">
                    <div className="w-12 h-12 bg-gray-600 rounded-full flex items-center justify-center animate-bounce">
                      <span className="text-white text-2xl">🔒</span>
                    </div>
                  </div>
                )}

                {/* Icono del módulo */}
                <div className="text-4xl mb-3 animate-bounce-gentle">
                  {module.icon.startsWith("./") || module.icon.startsWith("/") ? (
                    <img
                      src={module.icon}
                      alt={module.name}
                      className="w-16 h-16 object-contain mx-auto"
                      draggable={false}
                    />
                  ) : (
                    <span>{module.icon}</span>
                  )}
                </div>

                {/* Nombre del módulo */}
                <h3 className="text-lg font-bold text-gray-800 text-center mb-2">
                  {module.name}
                </h3>

                {/* Descripción */}
                <p className="text-sm text-gray-600 text-center mb-3">
                  {module.description}
                </p>

                {/* Barra de progreso */}
                {module.isUnlocked && module.progress > 0 && (
                  <div className="w-full bg-white/40 rounded-full h-2 mb-3">
                    <div 
                      className="bg-gradient-to-r from-green-400 to-blue-500 h-2 rounded-full transition-all duration-500"
                      style={{ width: `${module.progress}%` }}
                    ></div>
                  </div>
                )}

                {/* Progreso y estrellas */}
                {module.isUnlocked && (
                  <div className="flex items-center gap-2">
                    {[...Array(3)].map((_, i) => (
                      <Star
                        key={i}
                        className={`w-4 h-4 transition-all duration-300 ${
                          i < module.stars
                            ? "text-yellow-400 fill-current animate-pulse"
                            : "text-gray-300"
                        }`}
                      />
                    ))}
                  </div>
                )}

                {/* Efectos de partículas mejorados */}
                {module.isUnlocked && (
                  <>
                    <div className="absolute top-2 left-2 text-xs animate-bounce" style={{ animationDelay: "0.5s" }}>
                      ✨
                    </div>
                    <div className="absolute bottom-2 right-2 text-xs animate-bounce" style={{ animationDelay: "1s" }}>
                      🌟
                    </div>
                    <div className="absolute top-1/2 left-2 text-xs animate-bounce" style={{ animationDelay: "1.5s" }}>
                      💫
                    </div>
                  </>
                )}
              </div>
            </div>
          ))}
        </div>

        {/* Motivational Message mejorado */}
        <div className="text-center mt-12">
          <div className="bg-gradient-to-br from-white/90 to-purple-50/90 backdrop-blur-sm rounded-3xl p-8 shadow-xl max-w-2xl mx-auto animate-fade-in-up">
            <div className="flex items-center justify-center gap-4 mb-4">
              <Brain className="w-8 h-8 text-purple-600 animate-pulse" />
              <h3 className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-pink-600 bg-clip-text text-transparent">
                ¡Tu Cerebro Está Creciendo!
              </h3>
              <Brain className="w-8 h-8 text-purple-600 animate-pulse" />
            </div>
            <p className="text-gray-700 text-lg mb-4 leading-relaxed">
              Cada problema que resuelves fortalece tu mente y crea nuevas conexiones neuronales 🧠✨
            </p>
            <div className="bg-gradient-to-r from-yellow-100 to-orange-100 p-4 rounded-2xl border-2 border-yellow-200">
              <p className="text-orange-800 font-medium text-sm">
                🔬 <strong>Neurociencia para niños:</strong> Cuando practicas matemáticas, tu cerebro 
                libera dopamina, el químico de la felicidad, ¡haciéndote más inteligente y feliz!
              </p>
            </div>
            
            {/* Botón de audio motivacional */}
            <button 
              onClick={playMotivationalAudio}
              className="mt-6 bg-gradient-to-r from-green-400 to-blue-500 hover:from-green-500 hover:to-blue-600 text-white px-6 py-3 rounded-full font-bold shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 flex items-center gap-2 mx-auto"
            >
              <Volume2 className="w-5 h-5" />
              ¡Escucha un mensaje especial!
            </button>
          </div>
        </div>

        {/* Sección de neurociencia educativa */}
        <div className="mt-16 bg-gradient-to-br from-blue-50 to-purple-50 rounded-3xl p-8 shadow-xl">
          <div className="text-center mb-8">
            <h3 className="text-3xl font-bold text-gray-800 mb-4">
              🧠 ¿Sabías que tu cerebro es súper poderoso?
            </h3>
            <p className="text-lg text-gray-600">
              Descubre datos fascinantes sobre cómo aprendes matemáticas
            </p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl p-6 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up">
              <div className="text-4xl text-center mb-4">🚀</div>
              <h4 className="font-bold text-gray-800 mb-2 text-center">Velocidad Mental</h4>
              <p className="text-sm text-gray-600 text-center">
                Tu cerebro puede procesar información a 120 metros por segundo. ¡Eres súper rápido!
              </p>
            </div>
            
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl p-6 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up" style={{ animationDelay: "0.1s" }}>
              <div className="text-4xl text-center mb-4">🌟</div>
              <h4 className="font-bold text-gray-800 mb-2 text-center">Neuronas Activas</h4>
              <p className="text-sm text-gray-600 text-center">
                Cuando resuelves problemas matemáticos, activas más de 100 mil millones de neuronas.
              </p>
            </div>
            
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl p-6 shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-1 animate-slide-up" style={{ animationDelay: "0.2s" }}>
              <div className="text-4xl text-center mb-4">💪</div>
              <h4 className="font-bold text-gray-800 mb-2 text-center">Músculo Mental</h4>
              <p className="text-sm text-gray-600 text-center">
                Practicar matemáticas es como hacer ejercicio para tu cerebro. ¡Se vuelve más fuerte!
              </p>
            </div>
          </div>
        </div>
      </main>

      {/* Audio element (oculto) */}
      <audio ref={audioRef} preload="none" />

      {/* Estilos CSS personalizados */}
      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px); }
          50% { transform: translateY(-20px); }
        }

        @keyframes fade-in {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }

        @keyframes fade-in-delay {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }

        @keyframes fade-in-scale {
          from { opacity: 0; transform: translate(-50%, -50%) scale(0.8); }
          to { opacity: 1; transform: translate(-50%, -50%) scale(1); }
        }

        @keyframes fade-in-up {
          from { opacity: 0; transform: translateY(10px) scale(0.95); }
          to { opacity: 1; transform: translateY(0) scale(1); }
        }

        @keyframes slide-up {
          from { opacity: 0; transform: translateY(30px); }
          to { opacity: 1; transform: translateY(0); }
        }

        @keyframes slide-down {
          from { opacity: 0; transform: translateY(-10px) scale(0.95); }
          to { opacity: 1; transform: translateY(0) scale(1); }
        }

        @keyframes bounce-gentle {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-8px); }
        }

        @keyframes bounce-in {
          0% { transform: scale(0.3); opacity: 0; }
          50% { transform: scale(1.05); }
          70% { transform: scale(0.9); }
          100% { transform: scale(1); opacity: 1; }
        }

        @keyframes shake {
          0%, 100% { transform: translateX(0); }
          10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
          20%, 40%, 60%, 80% { transform: translateX(5px); }
        }

        @keyframes pulse-slow {
          0%, 100% { opacity: 1; }
          50% { opacity: 0.5; }
        }

        @keyframes spin-slow {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }

        @keyframes gradient {
          0%, 100% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
        }

        .animate-fade-in {
          animation: fade-in 1s ease-out;
        }

        .animate-fade-in-delay {
          animation: fade-in-delay 1s ease-out 0.3s both;
        }

        .animate-fade-in-scale {
          animation: fade-in-scale 0.5s ease-out;
        }

        .animate-fade-in-up {
          animation: fade-in-up 0.6s ease-out;
        }

        .animate-slide-up {
          animation: slide-up 0.6s ease-out both;
        }

        .animate-slide-down {
          animation: slide-down 0.2s ease-out;
        }

        .animate-bounce-gentle {
          animation: bounce-gentle 3s ease-in-out infinite;
        }

        .animate-bounce-in {
          animation: bounce-in 0.6s ease-out;
        }

        .animate-shake {
          animation: shake 0.6s ease-in-out;
        }

        .animate-pulse-slow {
          animation: pulse-slow 3s ease-in-out infinite;
        }

        .animate-spin-slow {
          animation: spin-slow 3s linear infinite;
        }

        .animate-gradient {
          background-size: 200% 200%;
          animation: gradient 3s ease infinite;
        }

        /* Mejoras adicionales para evitar bugs */
        .group:hover .group-hover\\:translate-x-full {
          transform: translateX(100%);
        }

        .transform {
          transform-origin: center;
        }

        /* Suavizar transiciones para evitar saltos */
        * {
          will-change: auto;
        }

        .transition-all {
          transition-property: all;
          transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        }

        /* Optimizar animaciones para mejor rendimiento */
        @media (prefers-reduced-motion: reduce) {
          *, *::before, *::after {
            animation-duration: 0.01ms !important;
            animation-iteration-count: 1 !important;
            transition-duration: 0.01ms !important;
          }
        }
      `}</style>
    </div>
  );
}